import React from "react";
import { Link, graphql } from "gatsby";
import Layout from "../components/layout";
import SEO from "../components/seo";
import BannerLanding from "../components/BannerLanding";

const BlogPost = props => {
  const { prev, next } = props.pageContext;
  return (
    <Layout>
      <SEO
        title={props.data.markdownRemark.frontmatter.title}
        description={props.data.markdownRemark.excerpt}
      />
      <BannerLanding
        title={bannerTitle(props)}
        content={props.data.markdownRemark.frontmatter.date}
      />

      <div id="main">
        <section id="one">
          <div className="inner">
            <div
              dangerouslySetInnerHTML={{
                __html: props.data.markdownRemark.html
              }}
            />
          <div className="inner" align="center">
            <h6>Tags:&nbsp;&nbsp;
              {props.data.markdownRemark.frontmatter.tags.map((tag, i) => (<>
              <Link to={"/tags/" + tag.replaceAll(' ', '-')} key={i} className="button">
                {tag}
              </Link>
                &nbsp;&nbsp;
              </>
            ))}
            </h6>
          </div>
          </div>
        </section>
        <div className="inner" align="center">
          <ul className="actions">
            <li>
              {prev && prev.frontmatter.published && (
                <Link
                  to={`/post${prev.fields.slug}`}
                  rel="prev"
                  className="button special fit text-left"
                >
                  ← Prev. Post
                </Link>
              )}
            </li>
            <li>
              {next && next.frontmatter.published && (
                <Link
                  to={`/post${next.fields.slug}`}
                  rel="next"
                  className="button special fit align-right"
                >
                  Next Post →
                </Link>
              )}
            </li>
          </ul>
        </div>
      </div>
    </Layout>
  );
};

const bannerTitle = props => (
  <h6>{props.data.markdownRemark.frontmatter.title}</h6>
);

export default BlogPost;

export const query = graphql`
  query($slug: String) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date(formatString: "DD MMMM, YYYY")
        tags
      }
      excerpt
    }
  }
`;
